.image-crop-preview .img-preview {
  float: left;
  margin-bottom: .5rem;
  margin-right: .5rem;
  overflow: hidden;
  background-color: #f1f5f7;
  text-align: center;
  width: 100%; }
  .image-crop-preview .img-preview > img {
    max-width: 100%; }

.image-crop-preview .preview-lg {
  height: 9rem;
  width: 16rem; }

.image-crop-preview .preview-md {
  height: 4.5rem;
  width: 8rem; }

.image-crop-preview .preview-sm {
  height: 2.25rem;
  width: 4rem; }

.image-crop-preview .preview-xs {
  height: 1.125rem;
  margin-right: 0;
  width: 2rem; }

.img-crop-preview-btns > .btn,
.img-crop-preview-btns > .btn-group {
  margin-bottom: 8px;
  margin-right: 8px; }

.docs-cropped .modal-body > img,
.docs-cropped .modal-body > canvas {
  max-width: 100%; }

.docs-drop-options {
  max-height: 400px;
  overflow-y: auto; }

.gmaps, .gmaps-panaroma {
  height: 300px;
  background: #f1f5f7;
  border-radius: 3px; }

.gmaps-overlay {
  display: block;
  text-align: center;
  color: #fff;
  font-size: 16px;
  line-height: 40px;
  background: #6658dd;
  border-radius: 4px;
  padding: 10px 20px; }

.gmaps-overlay_arrow {
  left: 50%;
  margin-left: -16px;
  width: 0;
  height: 0;
  position: absolute; }
  .gmaps-overlay_arrow.above {
    bottom: -15px;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    border-top: 16px solid #6658dd; }
  .gmaps-overlay_arrow.below {
    top: -15px;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    border-bottom: 16px solid #6658dd; }

.jvectormap-label {
  border: none;
  background: #323a46;
  color: #fff;
  font-family: "Cerebri Sans,sans-serif";
  font-size: 0.875rem;
  padding: 5px 8px; }

.mapael .map {
  position: relative; }
  .mapael .map .zoomIn {
    top: 25px; }
  .mapael .map .zoomOut {
    top: 50px; }

.mapael .mapTooltip {
  position: absolute;
  background-color: #6658dd;
  opacity: 0.95;
  border-radius: 3px;
  padding: 2px 10px;
  z-index: 1000;
  max-width: 200px;
  display: none;
  color: #fff;
  font-family: "Cerebri Sans,sans-serif"; }

.mapael .zoomIn,
.mapael .zoomOut,
.mapael .zoomReset {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  border-radius: 2px;
  font-weight: 500;
  cursor: pointer;
  background-color: #6658dd;
  text-decoration: none;
  color: #fff;
  font-size: 14px;
  position: absolute;
  top: 0;
  left: 10px;
  width: 24px;
  height: 24px;
  line-height: 24px; }

.mapael .plotLegend text {
  font-family: "Nunito", sans-serif !important; }
